export default {
  name: "cha_report_dialog",
  props: {
    show: undefined,
    cha_data: undefined
  },
  data() {
    return {
      description: '',
      selectedItem: 1,
      items: [
        {text: '联系方式不正确', 'type': "contact_error"},
        {text: '联系方式失效', 'type': 'contact_inactive'},
        {text: '本人不符合', 'type': 'not_match'},
        {text: '信息不正确', 'type': 'info_error'},

      ],
    }
  },
  created() {

  },
  methods: {
    on_report: function () {
      console.log('description:', this.description);
      console.log('selectedItem:', this.selectedItem);

      var post_data = {
        'description': this.description,
        'item': this.items[this.selectedItem]
      }

      console.log('post_data:', post_data)

      this.$axios.post('/app/cha/report', post_data).then((response) => {

      }).catch((error) => {
        console.log("error:", error);
      });

      this.show = false;
    }
  }
}
